import Vue from 'vue'
import App from './App.vue'
import router from './router'
import TransRoute from '@/components/TransRoute.vue'
import Prism from 'prismjs'
import VueWaypoint from 'vue-waypoint'

Vue.config.productionTip = false

Vue.component('TransRoute', TransRoute)
Vue.use(VueWaypoint)

Vue.prototype.$codify = async function prettify() {
  await Vue.prototype.$nextTick()
  Prism.highlightAll(false)
},

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
