<template>
  <div>
    <div class="header">
      <Title></Title>
    </div>
    <RecurNav>
    </RecurNav>
    <TransRoute></TransRoute>
    <div :class="footer_style">Made With &#x2764; and Vue.js</div>
  </div>
</template>

<script>
// @ is an alias to /src
import RecurNav from '@/components/RecurNav.vue'
import Title from '@/components/Title.vue'

export default {
  components: {
    RecurNav,
    Title,
  },
  watch: {
    '$route'(to, from) {
      this.$emit('test')
      if (to.name == 'Projects') {
        this.footer_style = 'footer-no-margin'
      }
      else if (from.name == 'Projects') {
        this.footer_style = 'footer'
      }
    }
  },
  data() {
    return {
      footer_style: 'footer'
    }
  },
  created() {
    if (this.$route.name == 'Frame') // TODO: or none of the listed routes
      this.$router.replace({name: 'About Me'})
  },
  mounted() {
    if (this.$route.name == 'Projects')
      this.footer_style = 'footer-no-margin'
  },
  methods: {
    assignStyle(prop, style) {
      this[prop] = style
    }
  }
}
</script>

<style lang="scss" scoped>
.footer{
  @extend .bordered;
  border-bottom: none; // overwrites
  padding-top: 0.5rem;
  margin-top: $indent;
}
.header {
  @extend .bottom;
}
.footer-no-margin {
  @extend .footer;
  margin-top: $indent - 1rem;
}
</style>
