<script>
export default {
  methods: {
      insertTags() {
      let titleHTML = document.querySelector('h1')
      titleHTML.insertAdjacentHTML("afterend", 
        `<div style="display: inline-flex; margin-bottom: 0.5rem;">
          <h3 style="margin: 0px;">Tags:</h3>
          <h5 class="tag-project"></h5>
        </div>`
      )
      let techHTML = document.querySelector('.tag-project')
      this.conf.tags.forEach(t => {
        techHTML.insertAdjacentHTML("beforeend", `<div style="align-self: center;" class="tag-item">${'• ' + t}</div>`);
      })
    },
  }
}
</script>