<template>
  <div>
    <vue-markdown v-if="src_ != null && !err" class="text-page" :source="src_" @rendered="$codify"></vue-markdown>
    <div v-else-if="err">Error Loading Doc for {{this.$route.name}}</div>
  </div> 
</template>

<script>
import VueMarkdown from 'vue-markdown'
import { getText } from '@/components/Fetchers.js'

export default {
  components: {
    VueMarkdown
  },
  props: {
    src: null,
    text_path: {type: String, default() { return this.$route.path.substr(1,) + '.md'}}, // $route.path returns the absolute path
  },
  watch: {
    // react to param changes
    '$route'() {
      this.created() 
    }
  },
  data() {
    return {
      src_: this.src,
      url: null,
      err: false,
    }
  },
  created() {
    this.created()
  },
  methods: {
    created() {
      if (this.src_ == null){
        getText(this.text_path)
          .then(resp => {
            this.src_ = resp
          })
          .catch(err => {
            this.err = true
            throw err
        })
      }
    }
  }
}
</script>
