<template>
  <keep-alive>
    <div class='recur-nav'
    @mouseleave="resetTimeout()"
    @mouseenter="stopTimeout">
      <div v-waypoint="{active: true, callback: resetScroll}"></div>
      <RecurTab v-for="path in paths" :key="path.name" :path_obj="path" :reset="reset" :level="0"></RecurTab>
    </div>
  </keep-alive>
</template>

<script>
import RecurTab from '@/components/RecurTab.vue'

export default {
  components: {
    RecurTab
  },
  data() {
    return {
      paths: null,
      reset: false,
      timeout: null,
    }
  },  
  created () {
    this.paths = this.$router.options.routes
    this.reset = false
  },
  methods: {
    resetTimeout() {
      this.timeout = setTimeout(() => { this.reset = !this.reset }, 500)
    },
    stopTimeout() {
      if (this.timeout !== null) {
        window.clearTimeout(this.timeout)
        this.timeout = null
      }
    },
    resetScroll(event) {
      if (event.going == 'out' && event.direction == 'top') {
        this.reset = !this.reset
      }
    }
  }
}
</script>

<style scoped lang="scss">
@import '~@/style/custom';

.recur-nav{
  @extend .bottom;
  @extend .bordered;
}
</style>
