<template>
  <div style="display: table-row;" v-if="conf != null" class="text-page">
    <div style="display: table-cell;">
      <div class="left">
        <router-link :to="{name: conf.name}">
          <h3>{{name}}</h3>
        </router-link>
        <vue-markdown v-if="short_desc != null" class="short-desc" :source="short_desc"></vue-markdown>
      </div>
    </div>
    <div style="display: table-cell; text-align: center;">
      <div class="tag right">
        <div v-for="t in conf.tags" :key="t" class="tag-item">{{'• ' + t}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSrc } from '@/components/Project.js'
import VueMarkdown from 'vue-markdown'

export default {
  components: {
    VueMarkdown
  },
  props: {
    conf: {type: Object, required: true},
    src: {},
  },
  data() {
    return {
      name: null,
      err: false,
      short_desc: null,
    }
  },
  created() {
    this.created()
  },
  methods: {
    async created() {
      if (this.src == null) {
        this.src = await getSrc(this.conf)
      } 
      
      this.parseSrc(this.src)
    },
    parseSrc() {
      this.name = this.src.split(/\n|$/, 1)[0].substr(2)
      this.short_desc = this.src.split(/\n|$/, 3).slice(1,).join('\n') // matches first paragraph (short description)
      this.short_desc = this.short_desc + (this.conf.source != undefined && this.conf.source != 'local' ? '\n' + `Github: [${this.conf.repo.replace(/https?:\/\/(?:www.)?/m, '')}](${this.conf.repo})` : '') // if not local add repo link
    }
  }
};
</script>

<style lang="scss" scoped>
.left {
  padding-right: 2rem;
}
.right {
  vertical-align: top;
  box-sizing: border-box;
  overflow: visible;
  display: flex;
  justify-content: space-between;
  align-content: stretch;
}
h3 {
  display: inline;
}
</style>
