import Vue from 'vue'
import Router from 'vue-router'
import Frame from '@/components/Frame.vue'
import TextPage from '@/components/TextPage.vue'
import BlogPage from '@/components/BlogPage.vue'
import List from '@/components/List.vue'
import ListView from '@/components/ListView.vue'
import Tetrust from '@/views/projects/tetrust/Tetrust.vue'
import { routeObjFromConf } from '@/components/Project.js'
import { sortConfsOnRoutes, getConfsList, getBlogs, getBlogsList } from './components/Project'

Vue.use(Router)

class CustomRouter extends Router {
  prependBase(path) {
    return `${this.options.base}${path}`
  }
}

const routes = [
  {
    path: '/',
    name: 'Frame',
    component: Frame,
    meta: { nav_show: false },
    children: [
      {
        path: 'about',
        name: 'About Me',
        component: TextPage,
      },
      {
        path: 'projects',
        name: 'Projects',
        component: List,
        props: { list_type: "Projects", lists: getConfsList, ListView },
        children: [
          routeObjFromConf("3D Scene Renderer"),
          routeObjFromConf("Fractal Tree Generator"),
          routeObjFromConf("Cool Compiler"),
          routeObjFromConf("NYC Open Data Profiling"),
          {
            path: 'tetrust',
            name: 'Tetrust',
            component: Tetrust,
          },
          routeObjFromConf("Geo NodeLink"),
          Object.defineProperty(
            routeObjFromConf("Personal Site"), 
            "children", {
              value: [{
                  path: 'recursive_navbar',
                  name: 'Recursive Navbar',
                  component: BlogPage,
                  props: { 
                    project: 'Personal Site',
                    blog_name: 'Recursive Navbar'
                  }
              }]
            }
          ),
        ]
      },
      {
        path: "blogs",
        name: "Blogs",
        component: List,
        props: { 
          list_type: "Blogs", 
          lists: async () => await getBlogsList(),
          ListView 
        },
      },
    ]
  },
]

const router = new CustomRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

// sort projects based on navbar ordering
sortConfsOnRoutes(routes)

export function changeTitleToPage(to, ) {
  document.title = `VN: ${to.name}`
}

router.afterEach(changeTitleToPage)

export default router