<template>
  <div class="title">
    <div class="hello">Hello{{middle_curr}}&nbsp;</div>
    <div class="boxed">
      <router-link :to="{name:'About Me'}"><div class="buffer">{{end_curr}}</div></router-link>
      <SocialButtons :loaded="loaded"></SocialButtons>
    </div>
  </div>
</template>


<script>
import SocialButtons from "@/components/SocialButtons.vue";
export default {
  components: {
    SocialButtons
  },
  data() {
    return {
      middle_curr: " World",
      middle: ", My Name Is",
      end_curr: "",
      end: "Vincent Nikolayev",
      curr_bin: 0,
      loaded: false
    };
  },
  mounted() {
    this.do();
  },
  methods: {
    async do() {
      await this.sleep(2000);
      let num_tries = 1;
      await this.replace("middle_curr", "middle", num_tries);
      await this.replace("end_curr", "end", num_tries);
      await this.sleepIter();
      this.loaded = true;
    },
    async replace(curr, end, num_tries) {
      for (let index = 0; index < this[end].length; index++) {
        let letter = this[end].charAt(index);

        for (let i = 0; i < num_tries; i++) {
          this[curr] = this.replaceAt(this[curr], index, this.generate_0_1());
          await this.sleepIter();
        }
        this[curr] = this.replaceAt(this[curr], index, letter);
      }
    },
    generateLetter() {
      return Math.round(Math.random()).toString();
    },
    generate_0_1() {
      this.curr_bin = ~this.curr_bin;
      return Math.abs(this.curr_bin)
        .toString()
        .substring(0);
    },
    replaceAt(string, index, replace) {
      return string.substring(0, index) + replace + string.substring(index + 1);
    },
    sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    },
    async sleepIter() {
      await this.sleep(100);
    }
  }
};
</script>

