<template>
  <div>
    <TransRoute>
      <div v-if="$route.name == conf.name">
        <!-- v-show must be used so that the dom elements exist and can be selected -->
        <vue-markdown    
        v-if="src != null"
        v-show="loaded && !err"
        :source="src"
        class="text-page project"
        @rendered="$codify">
        </vue-markdown>
        <div v-else-if="err">Error Loading README for {{name}}</div>
      </div>
    </TransRoute>
  </div>
</template>

<script>
import VueMarkdown from 'vue-markdown'
import { shortenURL } from '@/components/Fetchers.js'
import { getSrc } from '@/components/Project.js'
import Tags from '@/components/Tags.vue'

export default {
  mixins: [Tags],
  name: 'Project',
  components: {
    VueMarkdown
  },
  props: {
    conf: {type: Object, required: true},
    src_edit: Function,
  },
  data () {
    return {
      src: null,
      err: false,
      loaded: false,
    }
  },
  watch: {
    '$route'() {
      this.created()
    }
  },
  mounted () {
    this.created()
  },
  methods: {
    async created() {
      if (this.$route.name != this.conf.name) return
      
      this.src = null
      this.loaded = false
      this.err = false

      getSrc(this.conf).then(async src => {
        this.src = src

        this.insertRemote()

        if (this.src_edit !== undefined) { await this.src_edit(this) }

        this.$nextTick(() => {
          this.insertTags() // may be rendered regardless (async template issue)
          this.loaded = true
        })
      }).catch(err => {
        console.error(err)
        this.err = true
      })
    },
   insertRemote() {
      if (this.conf.source != 'local') {
        let first_line = this.src.indexOf('\n')
        let insert_area = this.src.indexOf('\n', first_line + 2)
        this.src = this.src.substring(0, insert_area + 1) + ('\n' + `Github: [${shortenURL(this.conf.repo)}](${this.conf.repo})`) + this.src.substring(insert_area, )
      }
    }
  }
}
</script>
