<template>
  <div>
    <TransRoute>
      <!-- lists class has relevant table styling -->
      <section class="lists" v-if="$route.name == list_type">
        <header style="display: table-row">
          <div style="display: table-cell;" class="list-title">{{list_type}}</div>
          <div style="display: table-cell;" class="tag-title">Tags</div>
        </header>
        <component :is="ListView" v-for="(list, i) in lists_" :key="i"
          :conf="'conf' in list ? list['conf'] : list"
          :src="'src' in list ? list['src'] : null">
        </component>
      </section>
    </TransRoute>
  </div>
</template>

<script>
export default {
  props: {
    ListView: Object,
    list_type: String,
    lists: Function,
  },
  data() {
    return {
      lists_: null,
    }
  },
  async created() {
    if (this.$route.name != this.list_type) return

    this.lists_ = await this.lists()
  },
};
</script>

<style lang="scss" scoped>
.title {
  font-size: 2rem;
}
.list-title {
  @extend .title;
  text-align: left;
  width: 70%;
}
.tag-title {
  @extend .title;
  text-align: center;
  width: 30%;
}
.lists {
  display: table;
  table-layout: fixed;
  width: 100%;
}
</style>