import { GitFetcher } from '@/components/Fetchers.js'
import { getText } from '@/components/Fetchers.js'
import projects from '@/projects_confs.json'
import Project from '@/components/Project.vue'

let projectsList = projects
const projectsDict = arrToDict(projects)

export async function getSrc(conf) {
  let src = null
  let path_name = getPathName(conf)
  if (conf.source == 'github') {
    src = await new GitFetcher(path_name, getBranch(conf)).fetch()
  } else if (conf.source == 'local') {
    src = await getLocalREADME(path_name)
  } else {
    throw 'Improper source for project passed'
  }

  return src
}

export function getLocalREADME(name) {
  return getText(`projects/${name}/${name}.md`)
}

export function getPathName(conf) {
  return conf.path_name != undefined ? conf.path_name : conf.name
}

export function getBranch(conf) {
  return conf.branch != undefined ? conf.branch : "master"
}

import yaml from 'js-yaml'

async function loadBlog(path) {
  let src = await getText(path)
  let start = src.search('#')
  let conf = yaml.safeLoadAll(src.substr(0, start+1))[0]
  src = src.substr(start, )
  return {conf, src}
}

let Blogs = loadBlogs()

async function loadBlogs() {
  let blogs = {}
  for (const conf of projectsList) {
    if (conf.blogs == undefined) continue;

    // if it has a blog field
    let project_blogs = []
    for (const blog of conf.blogs) {
      project_blogs.push(await loadBlog(`projects/${conf.path_name}/blogs/${blog.path_name}.md`))
    }

    blogs[conf.name] = project_blogs
  }
  return blogs
}

export async function getBlogs(project) {
  let _blogs = await Blogs
  return _blogs[project]
}

export async function getBlog(project, blog_name) {
  // iterative search
  for (const {conf, src} of await getBlogs(project)) {
    if (conf.name == blog_name) {
      return {conf, src}
    }
  }
  return null
}

export async function getBlogsList() {
  let _blogs = await Blogs
  return Object.values(_blogs).flat(1)
}

function arrToDict(projects) {
  let out = {}
  for (const project of projects) {
    out[project.name] = project
  }
  return out
}

export function getConfsList() {
  return projectsList
}

export function getConfsDict() {
  return projectsDict
}

export function getConf(project) {
  return getConfsDict()[project]
}

export function routeObjFromConf(project_name) {
  return {
    path: getConfsDict()[project_name].path_name,
    name: project_name,
    component: Project,
    props: { conf: getConfsDict()[project_name] },
    meta: { conf: getConfsDict()[project_name] },
  }
}

export function sortConfsOnRoutes(routes) {
  let out = []

  let recurse = (routes) => {
    if (routes == undefined) { return }
    if (!(routes instanceof Array)) { // is an Object, using not operator since Array can be an object?
      if (routes.children != undefined) { // if it has children then iterate over them
        routes = routes.children
      } else {
        return
      }
    }
    // recurse depth first
    for (const route of routes) {
      recurse(route)
      let conf = getConfsDict()[route.name]
      if (conf != undefined) { // if it is a project
        out.push(conf)
      }
    }
  }

  recurse(routes)
  projectsList = out // assign to global
}