<template>
  <div>
    <div class="menu-item">
    <router-link
    v-if="!this.meta() || !(path_obj.meta.nav_show === false)"
    v-show="displayed()"
    :class="{highlighted: selected}"
    :to="{'name': path_obj.name}"
    @click.native="clickGuard(path_obj.name)"
    :event="go ? 'click' : ''">
      {{ prettyName }}
    </router-link>
    </div>

    <div :class="selectedClass()"
    v-if="path_obj.children !== undefined" >
      <RecurTab
      @show_ancestors="showAncestors()"
      :show_as_child="hover"
      :reset="reset"
      :level="level+1"
      v-for="child in path_obj.children" :key="child.name" :path_obj="child">
      </RecurTab>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RecurTab',
  props: {
    path_obj: Object,
    show_as_child: {type: Boolean, default: false}, // child of hovered node
    reset: Boolean,
    level: {type: Number, default: -1},
  },
  data() {
    return {
      selected: false, // current route
      hover: false, // mouseover
      go: false,
      ancestor: false, // ancestor of a selected node
    }
  },
  created() { // page reload
    this.selectFromPath(false)
  },
  watch: {
    reset() {
      this.resetState()
    },
    '$route'() {
      this.resetState()
    }
  },
  methods: {
    resetData() {
      this.hover = false
      this.go = false
      this.ancestor = false
    },
    async resetState() {
      // reset state
      this.resetData()

      this.selectFromPath(true)
    },
    selectFromPath(show_ancestors) {
      if (this.$route.name == this.path_obj.name) {
        this.selected = true
        if (show_ancestors) this.$emit('show_ancestors')
      } else {
        this.selected = false
      }
    },
    // display all ancestors
    showAncestors() {
      this.ancestor = true
      this.$emit('show_ancestors')
    },
    displayed() {
      // this.level == 1 displays first level always (since root component is at level 0)
      return this.level == 1 || this.selected || this.hover || this.ancestor || this.show_as_child
    },
    selectedClass() {
      // this.level !== 0 because the style looks like its applied to the next level elements
      if (this.displayed() && this.level !== 0) {
        return 'bordered-cell'
      } else {
        return ''
      }
    },
    meta() {
      return this.path_obj.meta != undefined
    },
    async clickGuard(routeName) {
      if (this.path_obj.children === undefined || this.showingChildren()) {
        this.$router.push({name: routeName}).catch(e => console.warn(e))
      } else {
        this.go = true
      }
      // // hiding logic
      // if (this.hasChildren() && this.hover && this.$route.name == routeName) { // hide hovered link with children that is the current route
      //   this.resetData()
      // } else if (this.hasChildren()) { // do not hover link with no children
      //   this.hover = true
      // }
      this.hover = true // delete if uncommenting above
    },
    showingChildren() {
      return this.hover || this.singleChildSelected()
    },
    singleChildSelected() { // parent of single child selected
      return this.hasChildren() && this.path_obj.children.length == 1 && this.ancestor
    },
    hasChildren() {
      return this.path_obj.children !== undefined
    }
  },
  computed: {
    prettyName() {
      let name = this.meta() ? this.path_obj.meta.conf.name || this.path_obj.name : this.path_obj.name
      if (this.hasChildren() && this.showingChildren()) {
        return '– ' + name
      } else if (this.hasChildren() && !this.showingChildren()) {
        return '+ ' + name
      }
      return name
    }
  }
}
</script>

<style lang="scss" scoped>
a {
  text-align: left;
}
.highlighted {
  background-color: $highlighted-color;
  display: inline;
  padding: 0.2rem;
  border-radius: 0.5rem;
}
.menu-item {
  display: flex;
}
.padded-cell {
  padding-left: 3%;
  padding-right: 3%;
}
.bordered-cell {
  @extend .padded-cell;
  border-left: 1px solid $main-sec-color;
  margin-right: -1rem;
  padding-right: 1rem;
}
</style>
