<template>
  <TextPage :src="src" v-if="loaded"></TextPage>
</template>

<script>
import Tags from "@/components/Tags.vue";
import TextPage from "@/components/TextPage.vue";
import { getBlog } from "@/components/Project.js";

export default {
  mixins: [Tags],
  components: {
    TextPage
  },
  props: {
    project: { type: String, required: true },
    blog_name: { type: String, required: true }
  },
  data() {
    return {
      conf: null,
      src: null,
      loaded: false,
    };
  },
  async created() {
    let {conf, src} = await getBlog(this.project, this.blog_name)
    this.conf = conf
    this.src = src
    this.loaded = true
    this.$nextTick(() => {
      this.insertTags()
    })
  },
};
</script>

