import axios from 'axios'

class Fetcher {
  constructor(baseURL) {
    this.src = null
    this.baseURL = baseURL
  }

  async fetch() {
    return axios.get(this.baseURL + '/README.md')
      .then(resp => {
        this.src = resp.data
        this.src = insertBaseURLs(this.src, this.baseURL)

        return this.src
      })
      .catch(err => {
        throw err
      })
  }
}

export class GitFetcher extends Fetcher {
  constructor(projectName, branch = "master") {
    super(`https://raw.githubusercontent.com/vinceniko/${projectName}/${branch}`)
    this.projectName = projectName
    this.branch = branch
  }
}

export function insertBaseURLs(src, base) {
  src = src.replace(/src="(?!https?:\/\/(?:www.)?)/gm, `src="${base}/`) // replace img tag src
  // TODO: inefficient. replace scans the entire string. res just finds the string, and then it has to be found again in src. replacing in place wouldnt work since res would be an invalid reference after inserting into src
  for (let res of src.matchAll(/!\[.*\]\((.*)\)/gm)) {
    let local_url = res[1]
    src = src.replace(local_url, `${base}/${local_url}`)
  }
  return src // replace markdown image syntax source
}

export function shortenURL(url) {
  return url.replace(/https?:\/\/(?:www.)?/m, '')
}

export async function getFile(text_path) {
  return import(`@/${text_path}`).then(mod => mod.default)
}

export async function getText(text_path) {
  return getFile(`texts/${text_path}`)
}