<template>
  <div>
    <Project :conf="conf" :src_edit="embedDemo"></Project>
  </div>
</template>

<script>
// @ is an alias to /src
import Project from '@/components/Project.vue'
import Tetrust from '@/assets/projects/tetrust/tetrust-quicksilver.js'
import {changeTitleToPage} from '@/router.js'
import { getConf } from '@/components/Project.js'

export default {
  name: 'Tetrust',
  components: {
    Project
  },
  data() {
    return {
      conf: getConf("Tetrust"),
      embedDemo: async function(comp) {
        try {
          await Tetrust()
          comp.src = comp.src.replace(/<img.*\/>/m, 
                                    `<h2>Play It:</h2>
                                    <tetrust id="tetrust"></tetrust>`)
          await comp.$nextTick() // wait for dom to be rerendered after changing src
          document.querySelector('tetrust').appendChild(document.querySelector('canvas'))
        } catch(e) {
          console.error(e)

          await comp.$nextTick() // wait for dom to be rerendered after changing src
          comp.src = comp.src.replace('alt="preview"', 'alt="preview" class="canvas"')
          document.querySelector('canvas').remove()
        } finally {
          changeTitleToPage(this.$route)
        }
      }
    }
  }
}
</script>

<style lang="scss">
canvas,.canvas {
  padding-left: 0;
  padding-right: 0;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
  display: block;
  width: 17rem;
  outline: $main-sec-color 3px solid;
}

img {
  width: auto !important;
}
</style>
